import {getAuthUserData} from "./auth-reducer.ts";

const INITIALIZED_SUCCESS = '/app/INITIALIZED-SUCCESS'

export type InitialStateType = {
    initialized: boolean
}

const initialState: InitialStateType = {
    initialized: false
}

export default function appReducer(state = initialState, action: object): InitialStateType {
    switch (action.type) {
        case INITIALIZED_SUCCESS:
            return {
                ...state,
                initialized: true
            }
        default:
            return state
    }
}

type InitializedSuccessActionType = {
    type: typeof INITIALIZED_SUCCESS
}

export function initializedSuccess(): InitializedSuccessActionType { return {type: INITIALIZED_SUCCESS}}

export function initializeApp() {
    return (dispatch: any) => {
        try {
            const authUserPromise = dispatch(getAuthUserData())
            Promise.all([authUserPromise]).then(() => {
                dispatch(initializedSuccess())
            })
        } catch (e) {
            console.error(`initializeApp, error: ${e}`)
        }
    }
}