const SEND_MESSAGE = '/dialog/SEND-MESSAGE'
const UPDATE_NEW_MSG_TEXT = '/dialog/UPDATE-NEW-MSG-TEXT'

type CompanionsType = {
    id: number,
    name: string
}

type DialogType = {
    id: number,
    message: string,
    sender: string
}

const initialState = {
    companions: [
        {id: 1, name: 'DDD'},
        {id: 2, name: 'AAA'},
        {id: 3, name: 'WWW'}
    ] as Array<CompanionsType>,
    messages: [
        {id: 11, message: 'Some', sender: ''},
        {id: 21, message: 'Soe', sender: ''},
        {id: 31, message: 'Yo', sender: ''}
    ] as Array<DialogType>,
    newMsgText: null as string | null
}

export type InitialStateType = typeof initialState

export default function dialogsReducer (state = initialState, action: object): InitialStateType {
    switch (action.type) {
        case SEND_MESSAGE:
            const msgData = {
                id: Date.now(),
                message: state.newMsgText,
                sender: '',
            }
            return {
                ...state,
                newMsgText: '',
                messages: [...state.messages, msgData]
            }

        case UPDATE_NEW_MSG_TEXT:
            return {
                ...state,
                newMsgText: action.newMsgText
            }

        default:
            return state
    }
}

export function sendMessageCreator () {
    return {
        type: SEND_MESSAGE
    }
}


type UpdateNewMessageTextCreatorActionType = {
    type: typeof UPDATE_NEW_MSG_TEXT,
    newMsgText: string
}

export function updateNewMessageTextCreator (text: string): UpdateNewMessageTextCreatorActionType {
    return {
        type: UPDATE_NEW_MSG_TEXT,
        newMsgText: text
    }
}