import {profileAPI} from "../api/api";
import {ProfileType, PostType, PhotosType} from "../types/types";

const ADD_POST = '/profile/ADD-POST'
const UPDATE_NEW_POST_TEXT = '/profile/UPDATE-NEW-POST-TEXT'
const SET_USER_PROFILE ='/profile/SET-USER-PROFILE'
const SET_STATUS = '/profile/SET-STATUS'
const DELETE_POST = '/profile/DELETE-POST'
const SAVE_PHOTO_SUCCESS = '/profile/SAVE-PHOTO-SUCCESS'

const initialState = {
    posts: [
        {id: 12, post: 'Hey', date: '', likes: 1},
        {id: 22, post: 'Some', date: '', likes: 1},
        {id: 32, post: 'Data', date: '', likes: 1},
    ] as Array<PostType>,
    newPostText: '' as string | null,
    profile: null as ProfileType,
    status: null as string | null
}

export type InitialStateType = typeof initialState

export default function profileReducer(state = initialState, action: any): InitialStateType {
    switch (action.type) {
        case ADD_POST:
            const postInfo = {
                id: Date.now(),
                post: state.newPostText,
                date: new Date(),
                likes: Math.floor(Math.random() * 100)
            }
            return {
                ...state,
                newPostText: '',
                posts: [postInfo, ...state.posts]
            }
        case UPDATE_NEW_POST_TEXT:
            return {
                ...state,
                newPostText: action.text
            }
        case SET_USER_PROFILE:
            return {
                ...state,
                profile: action.profile
            }
        case SET_STATUS:
            return {
                ...state,
                status: action.status
            }
        case DELETE_POST:
            return {
                ...state,
                posts: state.posts.filter(post => post.id !== action.postId)
            }
        case SAVE_PHOTO_SUCCESS:
            return {
                ...state,
                profile: {...state.profile, photos: action.photos}
            }
        default:
            return state
    }
}

type AddPostActionCreatorActionType = {
    type: typeof ADD_POST
}
export function addPostActionCreator(): AddPostActionCreatorActionType {return {type: ADD_POST}}

type UpdateNewPostTextActionCreatorActionType = {
    type: typeof UPDATE_NEW_POST_TEXT,
    text: string
}
export function updateNewPostTextActionCreator(text: string): UpdateNewPostTextActionCreatorActionType {return {type: UPDATE_NEW_POST_TEXT, text}}

type SetUserProfileActionType = {
    type: typeof SET_USER_PROFILE,
    profile: ProfileType
}
export function setUserProfile(profile: ProfileType): SetUserProfileActionType {return {type: SET_USER_PROFILE, profile}}

type SetStatusActionType = {
    type: typeof SET_STATUS,
    status: string
}
export function setStatus(status: string): SetStatusActionType {return { type: SET_STATUS, status}}

type DeletePostActionType = {
    type: typeof DELETE_POST,
    postId: number
}
export function deletePost(postId: number): DeletePostActionType {return {type: DELETE_POST, postId}}

type SavePhotoSuccessActionType = {
    type: typeof SAVE_PHOTO_SUCCESS,
    photos: PhotosType
}
export function savePhotoSuccess(photos: PhotosType): SavePhotoSuccessActionType {return {type: SAVE_PHOTO_SUCCESS, photos}}



//THUNK

export function getUserProfile(userId) {
    return async (dispatch: any) => {
        try {
            const res = await profileAPI.getProfile(userId)
            dispatch(setUserProfile(res.data))
        } catch (e) {
            console.error(`getUserProfile ${userId}, error: ${e}`)
        }
    }
}

export function getStatus(userId: number) {
    return async (dispatch: any) => {
        try {
            const res = await profileAPI.getStatus(userId)
            dispatch(setStatus(res.data))
        } catch (e) {
            console.error(`getStatus userId: ${userId} error: ${e}`)
        }
    }
}

export function updateStatus(status: string) {
    return async (dispatch: any) => {
        try {
            const res = await profileAPI.updateStatus(status)
            if (res.data.resultCode === 0) {
                dispatch(setStatus(status))
            }
        } catch (e) {
            console.error(`updateStatus ${status}, error: ${e}`)
        }
    }
}

export function savePhoto(file: any) {
    return async (dispatch: any) => {
        try {
            const res = await profileAPI.savePhoto(file)
            if (res.data.resultCode === 0) {
                dispatch(savePhotoSuccess(res.data.data.photos))
            }
        } catch (e) {
            console.error(`savePhoto ${file}, error: ${e}`)
        }
    }
}

export function saveProfileData(profile: ProfileType) {
    return async (dispatch: any, getState: any) => {
        try {
            const res = await profileAPI.saveProfileData(profile)
            if (res.data.resultCode === 0) {
                dispatch(getUserProfile(getState().auth.userId))
            }
        } catch (e) {
            console.error(`saveProfileData ${profile}, error: ${e}`)
        }
    }
}